body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  margin: auto;
}
.App {
  text-align: center;
}

.noteboard-header {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 28px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 20px 0;
}

.noteboard-header.light {
  background-color: #677b69;
  color: #e2f8e5;
}
.noteboard-header.light > .add-note-btn {
  background-color: #677b69;
  color: #e2f8e5;
}

.noteboard-header.light > .change-theme-btn {
  background-color: #677b69;
  color: #e2f8e5;
}

.noteboard-header.dark {
  background-color: #29332a;
  color: #a6baa9;
}

.noteboard-header.dark > .add-note-btn {
  background-color: #29332a;
  color: #a6baa9;
}
 .noteboard-header.dark > .change-theme-btn {
  background-color: #29332a;
  color: #a6baa9;
 }


.all-notes {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 40px 0;
  height: 100vh;
}
.all-notes.light {
  background-color: #e9efe9;
}
.all-notes.dark {
  background-color: #425840;
}

 .note-item.light{
  background-color: #bec8bd;
  border: 1px solid #234f27;
}

.note-item.dark{
  border: 1px solid #798e7a;
  background-color: #20381e;
}

.note-item.dark > .note-delete-item {
  border: 1px solid #798e7a;
  background-color: #20381e;
  color: #b0c2ae;
}

.note-item.dark > div > .note-edit-item {
  border: 1px solid #798e7a;
  background-color: #20381e;
  color: #b0c2ae;
}

.note-item.dark>div>.note-item-text {
  color: #d6e2d5;
}

 .note-item.light>div>.note-item-text {
  color: #1c201c;
 }

.note-item {
  position: relative;
  width: 160px;
  height: 200px;
  border-radius: 4px;
  margin: 10px;

}


.add-note-btn, .change-theme-btn {
  border: 1px solid #b6c3b6;;
  border-radius: 4px;
  padding: 4px 8px;
  font-size: 16px;
}

.note-item-text{
  margin: 5px;
  padding: 5px;
  width: 135px;
  height: 140px;
  outline: none;
}

.note-delete-item {
  border: 1px solid green;
  background-color: rgb(228, 240, 228);
  border-radius: 50%;
  padding: 3px 8px;
  position: absolute;
  top: -15px;
  right: -15px;
}

.note-edit-item {
  border: 1px solid green;
  background-color: rgb(228, 240, 228);
  border-radius: 4px;
  padding: 4px 8px;
  
}

.note-edit-item:hover, 
.add-note-btn:hover, 
.note-delete-item:hover, 
.change-theme-btn:hover {
  cursor: pointer;
  opacity: 0.5;
}

.edit-item {
 height: 100vh;
}

.edit-item.light {
  background-color: #e9efe9;
}

.edit-item.dark {
  background-color: #425840;
}


.edit-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.edit-input {
  width: 200px;
  height: 200px;
}

.edit-btn {
  padding: 5px;
  width: 207px;
  margin: 10px;
  cursor: pointer;
  opacity: 0.5;
  border: 1px solid;
  border-radius: 4px;
}

.edit-btn.light {
  background-color: #677b69;
  color: #e2f8e5; 
}

.edit-btn.dark {
  background-color: #29332a;
  color: #ecffef;
}
 
